.container {
  display: flex;
  flex: 1;
  background: #F7F7F7;
}
.container .content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container .content .logoImg {
  display: block;
  width: 3.75rem;
  height: 3.75rem;
  border-radius: 0.5rem;
}
.container .content .appDesc {
  font-size: 0.75rem;
  color: #333;
  margin-top: 1.5rem;
}
.container .downloadBtn {
  margin-top: 1.25rem;
  width: 10rem;
  height: 2rem;
  border-radius: 1rem;
  background-color: deepskyblue;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  color: white;
}
